import {gsap, TimelineMax as Timeline, Power1 } from 'gsap';
gsap.registerPlugin(Timeline, Power1);

const getPageTransitionTimeline = (node, delay) => {
  
  const timeline = new Timeline({ paused: true });
  timeline
    .from(node, {duration: 4, autoAlpha: 0, ease: Power1.easeIn })
    .from('h2', {duration: delay, autoAlpha: 0, ease: Power1.easeIn })
    .from('h1', {duration: delay, autoAlpha: 0, ease: Power1.easeIn })
    .from('h3', {duration: delay, autoAlpha: 0, ease: Power1.easeIn });

  var chips = document.querySelectorAll('.e-chip');
  for(var i=0; i<chips.length; i++){
    timeline.from(chips[i], {duration: delay/2, autoAlpha: 0, ease: Power1.easeIn }, "+=0");
  }


  return timeline;
}

export const play = (pathname, node, appears) => {
  //console.log("PLAY" , pathname);
  const delay = 0.2
  const timeline = new Timeline({ paused: true });

  var chips = document.querySelectorAll('.emotions-container .e-chip');
  if(pathname == '/'){
    document.body.className='emotions';

    timeline.from(node, {duration: (delay*2), autoAlpha: 0, ease: Power1.easeIn })    
    for(var i=0; i<chips.length; i++){
      timeline.from(chips[i], {duration: delay/2, autoAlpha: 0, ease: Power1.easeIn }, "+=0");
    }    
  }else{
    timeline.from(node, {duration: (delay/2)*(chips.length||1), autoAlpha: 0, ease: Power1.easeIn })
    .from('.emotion-container h2', {duration: delay*3, autoAlpha: 0, ease: Power1.easeIn })
    .from('.emotion-container h1', {duration: delay*4, autoAlpha: 0, ease: Power1.easeIn })
    .from('.emotion-container h3', {duration: delay*4, autoAlpha: 0, ease: Power1.easeIn })
    .from('.emotion-container h3 i', {duration: delay*2, autoAlpha: 0, ease: Power1.easeIn })
    .from('.emotion-container .links', {duration: delay*3, autoAlpha: 0, ease: Power1.easeIn });
  }

  timeline.play();  
}

export const exit = (pathname, node, appears) => {
  //console.log("EXIT" , pathname);
  const delay = 0.2
  const timeline = new Timeline({ paused: true });

  if(pathname == '/'){
    timeline.to('.emotions-container h3',  {duration: delay, opacity: 0, ease: Power1.easeIn })
    var chips = document.querySelectorAll('.emotions-container .e-chip');
    for(var i=0; i<chips.length; i++){
      timeline.to(chips[i], {duration: delay/5, opacity: 0, ease: Power1.easeIn }, "+=0");
    }
    timeline.to(node,  {duration: delay, opacity: 0, display: "none", ease: Power1.easeIn })
  }else{}

  timeline.play(); 
}
