import React from 'react';
import {Switch,Route} from "react-router-dom";
import { Transition, TransitionGroup } from 'react-transition-group';
import {play, exit} from '../utils/timelines';

import Emotions from '../components/Emotions';
import Emotion from '../components/Emotion';
import Footer from '../components/Footer';

const Routes = ({data, pathname, location}) => {
    return (
        <>
        <TransitionGroup component={null}>
        <Transition
          appear={true}
          key={location.pathname}
          onEnter={(node, appears) => play(location.pathname, node, appears)}
          onExiting={(node, appears) => exit(location.pathname, node, appears)}
          timeout={{enter: 0, exit: location.pathname=='/' ? (data.emotions.length*400) : 0 }}
        >
          <Switch location={location}>
            <Route exact path="/"><Emotions  data={data}/></Route>
            <Route path="/:emotion"><Emotion  data={data}/></Route>
          </Switch>
        </Transition>                
      </TransitionGroup>
      <Footer/>
      </>
    )
}

export default Routes;


